body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #F6F6F8;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.App {
  height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  background-color:black;
  background-repeat: no-repeat;
  opacity: 0.9;
  /* margin: 10px;
  margin-top: 40px;
  padding: 30px; 
  box-shadow: -1px 2px 7px -2px #7d7b7d; */
}

.name__container{
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  background-color:black;
}

.subtext{
  color: #909090;
  font-size: 2em;
  margin-top: -10px;
  opacity: 0.7;
  /* font-family: 'Gothic A1', sans-serif; */
}

.name{
  color: #E8E8E8;
  font-size: 4em;
  /* font-family: 'Muli', sans-serif; */
  font-family: 'Teko', sans-serif;
  font-family: 'Gothic A1', sans-serif;
  /* content: "Read this: "; */
  font-weight: 100;
}

.firstChar{
  margin: auto 0;
  color: white;
  font-size: 4.5em;
  font-family: 'Muli', sans-serif;
}

.background{
  height: 100vh;
  width: 100%;
  position: absolute;
}

.menu__item{
  color: #909090;
  font-size: 1.1em;
  font-family: 'Muli', sans-serif;
  margin: 10px;
}

.menu__items{
  position: absolute;
}

.res{
  -webkit-animation: blink-animation 1s steps(10, start) infinite;
          animation: blink-animation 1s steps(10, start) infinite;
}

.cont{
  -webkit-animation: blink-animation 1s steps(200, start) infinite;
          animation: blink-animation 1s steps(200, start) infinite;
}

.cert {
  -webkit-animation: blink-animation 1s steps(150, start) infinite;
          animation: blink-animation 1s steps(150, start) infinite;
}

.ques {
  -webkit-animation: blink-animation 1s steps(400, start) infinite;
          animation: blink-animation 1s steps(400, start) infinite;
}

.sub {
  -webkit-animation: blink-animation 1s steps(100, start) infinite;
          animation: blink-animation 1s steps(100, start) infinite;
}

.menu__blinker {
  color: #83FFFF;
  height: 10px;
  width: 10px;
  opacity: 0.8;
}
@keyframes blink-animation {
  to {
    visibility: hidden;
  }
}
@-webkit-keyframes blink-animation {
  to {
    visibility: hidden;
  }
}

.menu__link{
  color: white !important;
  text-decoration: none;
  margin-left: 5px;
}

